@import url('https://fonts.googleapis.com/css?family=Cabin|Raleway:300&display=swap');

html, body, #root {
  height: 100%;
  width: 100%;
  background-color: #f1f1f1;
  font-family: 'Cabin', sans-serif;
  font-size: 14px;
  color: #455860;
  margin: 0;
  padding: 0;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
