@import url(https://fonts.googleapis.com/css?family=Cabin|Raleway:300&display=swap);
html, body, #root {
  height: 100%;
  width: 100%;
  background-color: #f1f1f1;
  font-family: 'Cabin', sans-serif;
  font-size: 14px;
  color: #455860;
  margin: 0;
  padding: 0;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/**
 * Palette
 * -------
 * #181818
 * #455860
 * #5e6f76
 * #f1f1f1
 * #ffffff
 */

.App {
  height: 100%;
  width: 100%;
  background-color: #f1f1f1;
  font-family: 'Cabin', sans-serif;
  font-size: 14px;
  color: #455860;
  margin: 0;
  padding: 0;
  overflow: hidden;
}


/**
 * Palette
 * -------
 * #181818
 * #455860
 * #5e6f76
 * #f1f1f1
 * #ffffff
 */

.MainContainer {
  height: 100%;
  width: calc(100% - 70px);
  background-color: #ffffff;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  display: inline-block;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 10px;
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
  -moz-transition: transform 300ms;
  -o-transition: transform 300ms;
  -webkit-transition: -webkit-transform 300ms;
  white-space: normal;
}

/**
 * Palette
 * -------
 * #181818
 * #455860
 * #5e6f76
 * #f1f1f1
 * #ffffff
 */

.Article {
  height: 350px;
  width: 250px;
  background-color: #ffffff;
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
  margin: 20px 10px 0;
  -webkit-transition: box-shadow 300ms, -webkit-transform 300ms;
  transition: box-shadow 300ms, -webkit-transform 300ms;
  transition: box-shadow 300ms, transform 300ms;
  transition: box-shadow 300ms, transform 300ms, -webkit-transform 300ms;
  -moz-transition: box-shadow 300ms, transform 300ms;
  -o-transition: box-shadow 300ms, transform 300ms;
  -webkit-transition: -webkit-box-shadow 300ms, -webkit-transform 300ms;
}

.Article:hover {
  box-shadow: 0 3px 7px #5e6f76;
  -moz-box-shadow: 0 3px 7px #5e6f76;
  -webkit-box-shadow: 0 3px 7px #5e6f76;
  transform: translateY(-10px);
  -webkit-transform: translateY(-10px);
}

.Article img {
  height: 150px;
  width: 100%;
}

.Article .contentContainer {
  padding: 5px;
}

.Article .title {
  color: #455860;
  font-family: 'Raleway', sans-serif;
  font-size: 24px;
  font-weight: 300;
  padding: 5px;
}

.Article .date {
  color: #455860;
  padding: 5px;
}

.Article .content {
  color: #455860;
  padding: 5px;
}

/**
 * Palette
 * -------
 * #181818
 * #455860
 * #5e6f76
 * #f1f1f1
 * #ffffff
 */

.NavDrawer {
  height: 100%;
  width: 200px;
  background-color: #455860;
  color: #f1f1f1;
  display: inline-block;
  float: left;
  font-family: 'Raleway', sans-serif;
  font-size: 12px;
  font-weight: 300;
  margin-right:-130px;
  z-index: 100;
}

.NavDrawer:hover ~ .MainContainer {
  transform: translateX(130px);
  -webkit-transform: translateX(130px);
}

.NavDrawer ul {
  height: 100%;
  list-style-type: none;
  margin: 0;
  overflow: hidden;
  padding: 0;
}

.NavDrawer ul li {
  height: 70px;
  width: 200px;
  cursor: pointer;
}

.NavDrawer ul li:hover {
  background-color: #f1f1f1;
  color: #455860;
}

.NavButton svg {
  height: 20px;
  width: 20px;
  display: inline-block;
  fill: #f1f1f1;
  font-size: 20px;
  line-height: 20px;
  margin: 25px;
  text-align: center;
  vertical-align: middle;
}

.NavButton:hover svg {
  fill: #455860;
}

.NavButton .label {
  height: 20px;
  width: 180px;
  line-height: 20px;
}

/**
 * Palette
 * -------
 * #181818
 * #455860
 * #5e6f76
 * #f1f1f1
 * #ffffff
 */

.NavLinks {
  height: 20px;
  padding: 10px;
  text-align: center;
}

.NavLinks .icon {
  height: 20px;
  padding: 0.1em;
  width: 20px;
  fill: #f1f1f1;
  font-size: 20px;
  transition: opacity 300ms;
  -webkit-transition: opacity 300ms;
}

.NavLinks .icon:hover {
  opacity: 0.5;
}

